import { Box, Button, ButtonGroup, Typography, } from "@mui/material";
import { Home as HomeIcon, CalendarToday, Chat, Favorite, Settings } from '@mui/icons-material';
import { Link } from "react-router-dom";
import { useState } from "react";

export default function Footer() {

    const [activeButton, setActiveButton] = useState('home');

    const handleButtonClick = (buttonName) => {
        setActiveButton(buttonName);
    };
    return (
        <Box sx={{ position: 'fixed', bottom: 15, left: 0, right: 0 }}>
            <ButtonGroup sx={{ display: 'flex', left: '15px', gap: '10px', justifyContent: 'space-between' }}>
                <Link to="/" onClick={() => handleButtonClick('home')}>
                    <Button sx={{ display: 'flex', flexDirection: 'column', border: 'none' }} >
                        <HomeIcon sx={{  color: activeButton === 'home' ? 'blue' : 'aqua', fontSize: '40px' }} />
                        <Typography sx={{ position: 'absolute', fontSize: '15px', top: '40px' }}>Главня</Typography>
                    </Button>
                </Link>
                <Link to="/calendar" onClick={() => handleButtonClick('calendar')}>
                    <Button sx={{ display: 'flex', flexDirection: 'column', border: 'none' }}>
                        <CalendarToday sx={{ color: activeButton === 'calendar' ? 'blue': 'aqua', fontSize: '40px' }} />
                        <Typography sx={{ position: 'absolute', fontSize: '15px', top: '40px' }}>Афиша</Typography>
                    </Button>
                </Link>
                <Link to="chat" onClick={() => handleButtonClick('chat')}>
                    <Button sx={{ display: 'flex', flexDirection: 'column', border: 'none' }}>
                        <Chat sx={{ color: activeButton === 'chat' ? 'blue': 'aqua', fontSize: '40px' }} />
                        <Typography sx={{ position: 'absolute', fontSize: '15px', top: '40px' }}>Чаты</Typography>
                    </Button>
                </Link>
                <Link to="favorite" onClick={() => handleButtonClick('favorite')}>
                    <Button sx={{ display: 'flex', flexDirection: 'column', border: 'none' }}>
                        <Favorite sx={{ color: activeButton === 'favorite' ? 'blue': 'aqua', fontSize: '40px' }} />
                        <Typography sx={{ position: 'absolute', fontSize: '13px', top: '40px' }}>Избранное</Typography>
                    </Button>
                </Link>
                <Link to="/profil" onClick={() => handleButtonClick('profil')}>
                    <Button sx={{ display: 'flex', flexDirection: 'column', border: 'none' }}>
                        <Settings sx={{ color: activeButton === 'profil' ? 'blue': 'aqua', fontSize: '40px' }} />
                        <Typography sx={{ position: 'absolute', fontSize: '13px', top: '40px' }}>Профиль</Typography>
                    </Button>
                </Link>
            </ButtonGroup>

        </Box>
    )
}
